import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { Ng2FlatpickrModule } from 'ng2-flatpickr';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { SharedModule } from 'src/app/shared/shared.module';
import { NgSelectModule } from '@ng-select/ng-select';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { AddProductModule } from 'src/app/common-module/add-product/add-product.module';
import { AddSalesOrderComponent } from './add-sales-order.component';
import { SalesOrderService } from 'src/app/service/sales-order.service';

const routes: Routes = [{
  path: '',
  component: AddSalesOrderComponent
}
]

@NgModule({
  declarations: [AddSalesOrderComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    AddProductModule,
    Ng2FlatpickrModule,
    FormsModule,
    ReactiveFormsModule,
    SharedModule,
    NgSelectModule,
    PaginationModule,
    FormValidationModule,
  ], providers: [SalesOrderService]
})
export class AddSalesOrderModule { }
